.review-bar {
  width: 80vw;
  height: 30vh;
  /* background-color: rgb(0, 0, 0);*/
  background-color: #ee70574d;
  background-blend-mode: overlay;
  background-image: url('C:\Users\Joar\Documents\Skola\Webbutveckling II\Uppgift6\daily-app\public\noise-texture2-wide-cured2.png');
  background-size: null;
  
  display: flex;
  flex-direction: column;
}

.review-bar-title {
  color: #ffff;
  padding-top: 20px;
  padding-left: 20px;
  margin-top: 0px;
}

.search-bar {
  padding: 6px;
  width: 350px;
  margin-left: 20px;
    
}

.search-button {
  font-size: medium;
  border: none;
  border-radius: 5px;
  padding: 3px 10px;
  background-color: #454545;
  color: #fff;
  cursor: pointer;
  margin-left: 6px;
}
  
.search-button:hover {
  background-color: #3D3D3D;
}

.popup-container {
  position: absolute;
  top: 140px; /* Adjust the distance from the top of the review bar */
  width: 79.2%;
  max-height: 300px; /* Set a fixed height for scrollable area */
  background-color: #454545;
  border-radius: 10px;
  overflow-y: auto; /* Enable vertical scrolling */
  padding: 5px 15px 10px 10px; /* Adjust padding for scrollbar space */
  z-index: 999; /* Ensure it stays on top */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  box-sizing: content-box; /* Ensure padding is outside the width */
}

.popup-container::-webkit-scrollbar {
  width: 12px;
}
  
.popup-container::-webkit-scrollbar-track {
  background: #454545;
  border-radius: 15px;
}

.popup-container::-webkit-scrollbar-thumb {
  background: #3D3D3D;
  border-radius: 12px;
}

.popup-container::-webkit-scrollbar-thumb:hover {
  background: #333333;
}

.search-results-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.search-result-item {
  display: flex;
  flex-direction: column;
  width: 150px;
  align-items: center;
  text-align: center;  
}

.result-poster {
  width: 75%;
  height: auto;
  margin-bottom: 10px;
}

